import {Layout} from "../../ui/layout/layout"
import styles from './partners.module.scss';

import {useEffect, useState} from "react";
import {PartnersCard} from './partnersCard/partnersCard';
import {PartnersOpen} from '../partnersOpen/partnersOpen';
import { useDispatch, useSelector } from "react-redux";
import { AppStateType } from "core/redux/store";
import {PartnersThunk} from 'modules/dashboard/partners/partnersThunk';

export const Partners = (): JSX.Element => {
  const partners = useSelector((state: AppStateType) => state.partners.partners);
  const [isPartnerChosen, setIsPartnerChosen] = useState(false);
  const dispatch = useDispatch()

  useEffect(() => {
      dispatch(PartnersThunk());
  }, [dispatch])

const getPartner = () => {
  setIsPartnerChosen(true);
}

    return (
        <Layout>
          {!isPartnerChosen && (
          <div className={styles.partnersPage}>
            <div className={styles.partnersList}>
            {partners.map((partner) => (
              <div key={partner.id} className={styles.itemWrapper}>
                <PartnersCard onClick={getPartner} image={partner.image} ruTitle={partner.ruName} />
              </div>
            ))}
            </div>
          </div>)}
          {isPartnerChosen && (<PartnersOpen />)}
        </Layout>
    )
}