import {createAsyncThunk} from '@reduxjs/toolkit';
import {baseRequestAxios} from 'api/baseRequest';
import {ResetPasswordResponse} from "api/type/recovery";

export const resetPassword = createAsyncThunk(
    'resetPassword',
    async (email: string) => {
        const response = await baseRequestAxios.post<ResetPasswordResponse>("/registration/restore-password", {
            email: email,
        })
        if (response.status === 200) {
            return response.data
        }
    }
)

