import {createAsyncThunk} from '@reduxjs/toolkit';
import {baseRequestAxios} from 'api/baseRequest';
import {UserResponse} from "api/type/user/user";

export const getUserDataThunk = createAsyncThunk(
    'getUserData',
    async () => {
        const response = await baseRequestAxios.get<UserResponse>("/api/user")
        if (response.status === 200) {
            return response.data;
        }
    }
)
