import {createAsyncThunk} from '@reduxjs/toolkit';
import {baseRequestAxios} from 'api/baseRequest';
import {WalletTransactionsRequest, WalletTransactionsResponse} from 'api/type/wallet/transactions';

export const WalletTransactionsThunk = createAsyncThunk(
    'WalletTransactions',
    async (requestData: WalletTransactionsRequest) => {
        const response = await baseRequestAxios.get<WalletTransactionsResponse>("/api/wallet/transactions", {
            params: requestData,
        })
        if (response.status === 200) {
            return response.data
        }
    }
)
