import {createSlice} from '@reduxjs/toolkit';
import {createClientThunk} from "./createClientThunk";

const createClientSlice = createSlice({
    name: 'createClient',
    initialState: {
        client_id: '',
        client_secret: '',
        error: false,
        loading: false
    },
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(createClientThunk.pending, (state) => {
            state.loading = true;
        });
        builder.addCase(createClientThunk.fulfilled, (state, action) => {
            if (action.payload) {
                state.client_id = action.payload.client_id
                state.client_secret = action.payload.client_secret
                state.loading = false
            }
        });
        builder.addCase(createClientThunk.rejected, (state) => {
            state.loading = false;
            state.error = true;
        });
    },

});

export const createClientReducer = createClientSlice.reducer;
