import {createAsyncThunk} from '@reduxjs/toolkit';
import {ValueInfoRequest} from "api/type/contracts/valueInfo";
import {baseRequestAxios} from "api/baseRequest";

export const contractValueInfoThunk = createAsyncThunk(
    'contractValueInfoThunk',
    async () => {
        const response = await baseRequestAxios.get<ValueInfoRequest>("/api/contract/value-info")
        if (response.status === 200) {
            return response.data
        }
    }
)
