import {createSlice} from '@reduxjs/toolkit';
import {getNotificationThunk} from './getNotificationThunk';
import {GetNotificationResponse} from "api/type/user/getNotification";

const getNotificationSlice = createSlice({
    name: 'notification',
    initialState: {
        notification: {} as GetNotificationResponse,
        loading: false,
        error: false,
    },
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(getNotificationThunk.pending, (state) => {
            state.loading = true
        });
        builder.addCase(getNotificationThunk.fulfilled, (state, action) => {
            if (action.payload) {
                state.notification = action.payload
                state.loading = false;
            }
        });
        builder.addCase(getNotificationThunk.rejected, (state) => {
            state.loading = false;
            state.error = true
        });
    },
});

export const getNotificationReducer = getNotificationSlice.reducer;