import {ReactSVG} from "react-svg";
import style from './closePopup.module.scss';
import closePopup from '../../../pages/contacts/icons/XBox.svg';

interface Props {
    onClose?: () => void
}

export const ClosePopup = ({onClose}: Props): JSX.Element => {
    return (
        <div className={style.closePopup}>
            <button onClick={onClose}>
                <ReactSVG src={closePopup} />
            </button>
        </div>
    )
}