import {createSlice} from '@reduxjs/toolkit';
import {createInheritanceThunk} from './createInheritanceThunk';

const createInheritanceSlice = createSlice({
    name: 'createInheritance',
    initialState: {
        success: false,
        loading: false,
        error: false
    },
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(createInheritanceThunk.pending, (state) => {
            state.loading = true
        });
        builder.addCase(createInheritanceThunk.fulfilled, (state, action) => {
            if (action.payload) {
                state.success = action.payload.success;
                state.loading = false
            }
        });
        builder.addCase(createInheritanceThunk.rejected, (state, action) => {
            console.log(action)
            state.loading = false;
            state.error = true
        });
    },
});

export const createInheritanceReducer = createInheritanceSlice.reducer;