import styles from './statistic.module.scss';
import {PieChart, Pie, Cell} from "recharts";
import {ReactSVG} from 'react-svg';
import contractsIcon from '../icons/contracts.svg';
import takeOutIcon from '../icons/takeOut.svg';
import enterIcon from '../icons/enter.svg';
import structureIcon from '../icons/structure.svg';

const COLORS = ["#7A5BBE", "#EACB5F", "#4AB79B", "#5BBED9"];

const RADIAN = Math.PI / 180;
const renderCustomizedLabel = ({
                                   cx,
                                   cy,
                                   midAngle,
                                   innerRadius,
                                   outerRadius,
                                   percent,
                                   index,
                               }: any) => {
    const radius = innerRadius + (outerRadius - innerRadius) * 0.5;
    const x = cx + radius * Math.cos(-midAngle * RADIAN);
    const y = cy + radius * Math.sin(-midAngle * RADIAN);

    return (
        <text
            x={x}
            y={y}
            fill="white"
            textAnchor={x > cx ? "start" : "end"}
            dominantBaseline="central"
        >
            {percent > 0 && (`${(percent * 100).toFixed(0)}%`)}
        </text>
    );
};

interface Props {
    items: Transaction[];
}

export const Statistic = ({items}: Props) => {
    const getAmountOfRefill = (items: Transaction[]) => {
        if (items) {
            items = items.filter(item => item.type === 0);
            if (items.length > 1) {
                return items.map(item => item.amount).reduce(function (a, b) {
                    return a + b;
                })
            }
            return 0;
        }
    }

    const getAmountOfWithdrawal = (items: Transaction[]) => {
        if (items) {
            items = items.filter(item => item.type === 1);
            if (items.length > 1) {
                return items.map(item => item.amount).reduce(function (a, b) {
                    return a + b;
                })
            }
            return 0;
        }
    }

    const getAmountOfContract = (items: Transaction[]) => {
        if (items) {
            items = items.filter(item => item.type === 5);
            if (items.length > 1) {
                return items.map(item => item.amount).reduce(function (a, b) {
                    return a + b;
                })
            }
            return 0;
        }
    }

    const getAmountOfStructure = (items: Transaction[]) => {
        if (items) {
            items = items.filter(item => item.type === 3 || item.type === 4 || item.type === 7 || item.type === 8);
            if (items.length > 1) {
                return items.map(item => item.amount).reduce(function (a, b) {
                    return a + b;
                })
            }
            return 0;
        }
    }

    const data = [
        {name: "Пополнение", value: getAmountOfRefill(items) || 0, icon: enterIcon},
        {name: "Контракты", value: getAmountOfContract(items) || 0, icon: contractsIcon},
        {name: "Структура", value: getAmountOfStructure(items) || 0, icon: structureIcon},
        {name: "Вывод", value: getAmountOfWithdrawal(items) || 0, icon: takeOutIcon}
    ]
    return (
        <div className={styles.Statistic}>
            <h2 className={styles.title}>Статистика</h2>
            <div className={styles.diagramBlock}>
                <div className={styles.diagram}>
                    <PieChart className={styles.PieChart} width={200} height={200}>
                        <Pie
                            legendType="diamond"
                            data={data}
                            labelLine={false}
                            label={renderCustomizedLabel}
                            outerRadius={80}
                            fill="#8884d8"
                            dataKey="value"
                        >
                            {data.map((entry, index) => (
                                <Cell key={`cell-${index}`} fill={COLORS[index % COLORS.length]}/>
                            ))}
                        </Pie>
                    </PieChart>
                </div>
                <ul className={styles.diagramList}>
                    {[...data].sort((a, b) => b.value - a.value).map(item => (
                        <li className={styles.diagramItem} key={item.name}>
                            <div className={styles.itemName}>
                                <ReactSVG className={styles.Icon} src={item.icon}/>
                                <p>{item.name}</p>
                            </div>
                            <p>{Math.round(item.value)} DNC</p>
                        </li>
                    ))}
                </ul>
            </div>
        </div>
    )
}
