import {createSlice} from '@reduxjs/toolkit';
import {authorizations} from './authorizationThunk';

const authorizationSlice = createSlice({
    name: 'authorization',
    initialState: {
        isAuth: false,
        error: false,
        refresh_token: '',
        access_token: '',
        expires_in: '',
        token_type: 'bearer',
        grant_type: 'password' as const,
        scope: null,
        loading: false,
    },
    reducers: {
        signOut: (state) => {
            state.isAuth = false;
            sessionStorage.clear()
        },
    },
    extraReducers: (builder) => {
        builder.addCase(authorizations.pending, (state) => {
            state.loading = true;
        });
        builder.addCase(authorizations.fulfilled, (state, action) => {
            if (action.payload) {
                state.access_token = action.payload.access_token
                state.scope = action.payload.scope
                state.expires_in = action.payload.expires_in
                state.token_type = action.payload.token_type
                state.isAuth = true

                sessionStorage.setItem('token', action.payload.access_token);
            }
        });
        builder.addCase(authorizations.rejected, (state) => {
            state.loading = false;
            state.error = true;
        });
    },

});

export const {signOut} = authorizationSlice.actions;
export const authorizationReducer = authorizationSlice.reducer;
