import {createAsyncThunk} from '@reduxjs/toolkit';
import {baseRequestAxios} from 'api/baseRequest';
import {StructureLines} from "api/type/structure";

export const structureLines = createAsyncThunk(
    'structureLines',
    async () => {
        const response = await baseRequestAxios.get<StructureLines>("/api/structure/lines")
        if (response.status === 200) {
            return response.data
        }
    }
)
