import {baseUrl} from "api/baseRequest";
import fakeAvatar from "pages/dashboard/icons/fakeAvatar.jpg";

interface Props {
    avatar: string | null | undefined
}

export const ShowAvatar = ({avatar}: Props): JSX.Element => {

    return (
        <>
            {avatar ?
                (<img src={`${baseUrl}${avatar}`} alt="newsImage"/>) :
                (<img src={fakeAvatar} alt="avatar"/>)
            }
        </>
    )
}