import {createSlice} from '@reduxjs/toolkit';
import {axiosHeadersUpdater} from 'api/baseRequest';
import {RejectContractResponse} from 'api/type/contracts/rejectContract';
import {rejectContractThunk} from './rejectContractThunk';

const rejectContractSlice = createSlice({
    name: 'rejectContract',
    initialState: {
        rejectContract: {} as RejectContractResponse,
        loading: false,
        error: false
    },
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(rejectContractThunk.pending, (state) => {
            state.loading = true;
            axiosHeadersUpdater()
        });
        builder.addCase(rejectContractThunk.fulfilled, (state, action) => {
            if (action.payload) {
                state.rejectContract = action.payload;
            }
        });
        builder.addCase(rejectContractThunk.rejected, (state) => {
            state.loading = false
            state.error = true
        });
    },

});

export const rejectContractReducer = rejectContractSlice.reducer;