import {createAsyncThunk} from '@reduxjs/toolkit';
import {baseRequestAxios} from 'api/baseRequest';
import {UserUpdateData} from "api/type/user/user";

export const updateUserDataThunk = createAsyncThunk(
    'updateUserData',
    async (data: UserUpdateData) => {
        const response = await baseRequestAxios.post<{ success: boolean }>("/api/user", {
            country: data.country,
            phone: data.phone,
            email: data.email,
            onSuccessWithdrawTg: data.onSuccessWithdrawTg,
            onSuccessWithdrawMail: data.onSuccessWithdrawMail,
            onWebinarsMail: data.onWebinarsMail,
            onWebinarsTg: data.onWebinarsTg,
            onStartBonusMail: data.onStartBonusMail,
            onStartBonusTg: data.onStartBonusTg,
            onNewsMail: data.onNewsMail,
            onNewsTg: data.onNewsTg,
            onNewItemsInCalendarMail: data.onNewItemsInCalendarMail,
            onNewItemsInCalendarTg: data.onNewItemsInCalendarTg,
            telegram: data.telegram,
            facebook: data.facebook,
            instagram: data.instagram,
            onContractIncomeMail: data.onContractIncomeMail,
            onContractIncomeTg: data.onContractIncomeTg,
            onCourseMail: data.onCourseMail,
            onCourseTg: data.onCourseTg,
            twitter: data.twitter,
            onEventMail: data.onEventMail,
            onEventTg: data.onEventTg,
            onIncomeMail: data.onIncomeMail,
            onIncomeTg: data.onIncomeTg
        })
        if (response.status === 200) {
            return response.data;
        }
    }
)
