import {FC} from 'react';
import styles from './BuyButton.module.scss';

type Props = {
    onClick: any;
    color: 'green' | 'gold' | 'brown' | 'blue';
    disabled?: boolean;
}

const BuyButton: FC<Props> = (props): JSX.Element => {
    const {onClick, color, children, disabled} = props;

    return (
        <button disabled={disabled}
            type='button'
            className={`${styles.button} ${styles[color]} ${disabled? styles.disabled : ''}`}
            onClick={onClick}
        >
            <span>{children}</span>
        </button>
    )
}

export default BuyButton;
