import { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';

import {DNCBalance} from '../../dashboard/DNCBalance/DNCBalance';
import {TotalContracts} from "../../dashboard/TotalContracts/TotalContractsSample/TotalContracts";
import { BuyContracts } from '../buyContracts/BuyContracts/BuyContracts';
import { ContractList } from '../ContractList/ContractList';
import { IncomeList } from '../IncomeList/IncomeList';
import {ButtonsGroup} from "ui/buttons/buttonsGroup/buttonsGroup";
import DNCBalanceIcon from 'pages/dashboard/icons/DNCBalance.svg';
import RZMBalanceIcon from 'pages/dashboard/icons/RZMBalance.svg';

import notebookIcon from 'pages/dashboard/icons/notebook.svg';
import { AppStateType } from '../../../core/redux/store';
import { getUserDataThunk } from 'modules/user/getUserData/getUserDataThunk';
import { contractsThunk } from 'modules/contracts/contractsThunk';
import styles from './smartComponent.module.scss';
import { Popup } from 'ui/popup/popup';
import { BuyFirstPopup } from 'pages/dashboard/popups/buyFirstPopup/buyFirstPopup';
import { WarningPopup } from 'pages/dashboard/popups/warningPopup/warningPopup';
import { Withdraw } from 'ui/popup/withdraw/withdraw';
import { BuyCoins } from 'ui/popup/buyCoins/BuyCoins';

export const SmartComponent = () => {
  const {
    balance,
    rzmBalance,
    amountOfContract
} = useSelector((state: AppStateType) => state.user.user);
  const { items, count } = useSelector((state: AppStateType) => state.contracts.contracts);
  const [isContractBought, setIsContractBought] = useState(false);
  const [activeFirstPopup, setActiveFirstPopup] = useState(false)
  const [activeWarningPopup, setActiveWarningPopup] = useState(false)
  const [withdrawDnc, setWithdrawDnc] = useState(false)
  const [withdrawRzm, setWithdrawRzm] = useState(false)
  const [byDnc, setByDnc] = useState(false)
  const [byRzm, setByRzm] = useState(false)
  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(getUserDataThunk());
    dispatch(contractsThunk({
      contract_type: 3,
      limit: 10000,
      offset: 0,
    }));
    setIsContractBought(false);
  }, [isContractBought, balance, dispatch])

  const handleIsContractBought = () => {
    setIsContractBought(true);
  }

  
  const DNC = {
    by: (value: boolean) => setByDnc(value),
    takeOut: (value: boolean) => setWithdrawDnc(value),
}

const RZM = {
    by: (value: boolean) => setByRzm(value),
    takeOut: (value: boolean) => setWithdrawRzm(value),
}

const showFirstPopup = () => {
    setActiveFirstPopup(prev => !prev)
}

const showWarningPopup = () => {
    setActiveWarningPopup(prev => !prev)
}

const nextPopup = (value: 'policy1' | 'policy2') => {
    setActiveFirstPopup(false)

    if (value === 'policy2') {
        DNC.by(true)
    } else {
        setActiveWarningPopup(true)
    }
}

  return (
    <>
      {count && (
        <div className={styles.smartPage}>
          <div className={styles.PanelBlock}>
          <div className={styles.balance}>
              <DNCBalance balance={balance} title={'Баланс DNC'} income={0} icon={DNCBalanceIcon}>
                <ButtonsGroup onClickToSaleButton={showFirstPopup}
                              onClickToShowButton={() => DNC.takeOut(true)}/>
              </DNCBalance>

              <DNCBalance balance={rzmBalance} title={'Баланс RZM'} income={124} icon={RZMBalanceIcon}>
                <ButtonsGroup onClickToSaleButton={showFirstPopup}
                              onClickToShowButton={() => RZM.takeOut(true)}/>
              </DNCBalance>
          </div>
          <div className={styles.otherBlocks}>
              <div className={styles.contracts}>
                  <TotalContracts text='Общая сумма контрактов' sum={amountOfContract} icon={notebookIcon}/>
                  <TotalContracts text='Всего контрактов' sum={count} icon={notebookIcon}/>
              </div>
              <div className={styles.BuyContracts}>
                  <BuyContracts handleIsContractBought={handleIsContractBought}/>
              </div>
          </div>
          </div>
          <h2 className={styles.title}>
            Доход по контрактам
          </h2>
          <IncomeList items={items} count={count}/>
          <h2 className={styles.title}>
            Мои контракты
          </h2>
          <ContractList items={items} count={count}/>
        </div>
      )}
        {activeFirstPopup && (<Popup>
      <BuyFirstPopup nextStep={nextPopup} closePopup={showFirstPopup}/>
  </Popup>)}

  {activeWarningPopup && (<Popup>
      <WarningPopup nextStep={showWarningPopup} closePopup={showWarningPopup}/>
  </Popup>)}

  {byDnc && (<Popup>
      <BuyCoins closePopup={() => DNC.by(false)} icon={DNCBalanceIcon} nextStep={() => {
      }} title={'DNC'}/>
  </Popup>)}


  {withdrawDnc && (<Popup>
    <Withdraw closePopup={() => DNC.takeOut(false)} icon={DNCBalanceIcon} nextStep={() => {
      }} title={'DNC'}/>
  </Popup>)}

  {byRzm && (<Popup>
      <BuyCoins closePopup={() => RZM.by(false)} icon={RZMBalanceIcon} nextStep={() => {
      }} title={'RZM'}/>
  </Popup>)}
  {withdrawRzm && (<Popup>
    <Withdraw closePopup={() => RZM.takeOut(false)} icon={RZMBalanceIcon} nextStep={() => {
      }} title={'RZM'}/>
  </Popup>)}
  </>
  )
}
