import {createSlice} from '@reduxjs/toolkit';
import {VerificationFirstStepResponse} from 'api/type/verification/verificationFirstStep';
import {verificationFirstStepThunk} from './verificationFirstStepThunk';

const verificationFirstStepSlice = createSlice({
    name: 'verificationFirstStep',
    initialState: {
      verificationFirstStep: {} as VerificationFirstStepResponse,
      loading: false,
        error: false
    },
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(verificationFirstStepThunk.pending, (state) => {
            state.loading = true;
        });
        builder.addCase(verificationFirstStepThunk.fulfilled, (state, action) => {
            if (action.payload) {
                state.verificationFirstStep.status = action.payload.status;
                state.loading = false
            }
        });
        builder.addCase(verificationFirstStepThunk.rejected, (state) => {
            state.loading = false
            state.error = true
        });
    },

});

export const verificationFirstStepReducer = verificationFirstStepSlice.reducer;