import {createAsyncThunk} from '@reduxjs/toolkit';
import {baseRequestAxios} from 'api/baseRequest';
import {GetNotificationRequest, GetNotificationResponse} from "api/type/user/getNotification";

export const getNotificationThunk = createAsyncThunk(
    'getNotification',
    async (data: GetNotificationRequest) => {
        const response = await baseRequestAxios.get<GetNotificationResponse>("/api/notification/list", {
            params: data
        })
        if (response.status === 200) {
            return response.data;
        }
    }
)
