import {createSlice} from '@reduxjs/toolkit';
import {transactionsResponse} from 'api/type/contracts/contract.transaction';
import {transactionsThunk} from './transactionsThunk';

const transactionsSlice = createSlice({
    name: 'transactions',
    initialState: {
        transactions: {} as transactionsResponse,
        loading: false,
        error: false
    },
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(transactionsThunk.pending, (state) => {
            state.loading = true;
        });
        builder.addCase(transactionsThunk.fulfilled, (state, action) => {
            if (action.payload) {
                state.transactions = action.payload;
                state.loading = false
            }
        });
        builder.addCase(transactionsThunk.rejected, (state) => {
            state.loading = false
            state.error = true
        });
    },

});

export const transactionsReducer = transactionsSlice.reducer;