import {useState} from "react";
import {SwitchButton} from "../buttons/swichButton/switchButton";
import iconBell2 from "pages/settings/icon/iconBell2.svg";
import style from "../../pages/settings/components/pages/notifications/notifications.module.scss";

interface Props {
    onChange: (value: boolean) => void,
    activeValue: string,
    passiveValue: string,
    active: boolean
}

export const SwitchBlock = ({activeValue, passiveValue, onChange, active}: Props): JSX.Element => {
    const [isActiveNotifications, setIsActiveNotifications] = useState(false)

    const onChangeActiveNotifications = () => {
        setIsActiveNotifications(prev => !prev)
        onChange(isActiveNotifications)
    }
    return (
        <div className={style.switchBlock}>
            <span>{`${isActiveNotifications? passiveValue : activeValue}`}</span>
            <SwitchButton active={active} onChange={onChangeActiveNotifications} icon={iconBell2}/>
        </div>
    )
}