import {createAsyncThunk} from '@reduxjs/toolkit';
import {baseRequestAxios} from 'api/baseRequest';
import {BonusesTransactions} from "api/type/structure";

export const structureBonusesTransactions = createAsyncThunk(
    'structureBonusesTransactions',
    async (filter: { limit: number, offset: number }) => {

        const response = await baseRequestAxios.get<BonusesTransactions>("/api/structure/bonuses-transactions", {
            params: filter
        })
        if (response.status === 200) {
            return response.data
        }
    }
)
