import {useEffect, useState} from "react";
import {useDispatch, useSelector} from "react-redux";
import {AppStateType} from "core/redux/store";
import {NavLink} from "react-router-dom";
import {countries} from "../../../../../selectCountries/countries/countries";
import {OptionsLabel} from "../../../../../selectCountries/selectCountries";
import {signOut} from "modules/authorization/authorization/authorizationSlice";
import style from './exit.module.scss';
import emailImg from '../../image/emailImg.svg';
import phoneImg from '../../image/phoneImg.svg';
import {ReactSVG} from "react-svg";

export const Exit = (): JSX.Element => {
  const dispatch = useDispatch()
    const {email, phone, country} = useSelector((state: AppStateType) => state.user.user)
    const [sity, setSity] = useState<OptionsLabel | null>(null)

    useEffect(() => {
        if (countries) {
            countries.find((item) => item.value === country ? setSity(item) : null)
        }
    }, [country])

  const exit = () => {
      dispatch(signOut())
  }

    return (
        <div className={style.exit}>
            {sity && (<div className={style.location}>
              <img src={sity.src} className={style.sity} alt="sity"/>
                <span>{sity.label.ru}</span>
            </div>)}
            <span className={style.item}><ReactSVG className={style.icon} src={emailImg} />{email}</span>
            <span className={style.item}><ReactSVG className={style.icon} src={phoneImg} />{phone}</span>
            <NavLink onClick={exit} className={style.exitBtn} to={'/login'}>Выйти</NavLink>
        </div>
    )
}

