import style from './inputRadio.module.scss'

interface Props<T> {
    onChange: (value: T) => void;
    name: T;
    value: T,
    checked: boolean;
}

export function InputRadio<T extends string> ({value, name, checked, onChange}: Props<T>): JSX.Element {

    return (
        <div className={style.inputRadio}>
            <input checked={checked} onChange={() => onChange(value)}
                   type="radio"
                   name={name}
                   id={name!}/>
        </div>
    )
}