import {createAsyncThunk} from '@reduxjs/toolkit';
import {baseRequestAxios} from 'api/baseRequest';
import {PartnerSiteRequest, PartnerSiteResponse} from 'api/type/partnersSites';

export const partnersSiteThunk = createAsyncThunk(
    'partnersSite',
    async (data: PartnerSiteRequest) => {
        const response = await baseRequestAxios.get<PartnerSiteResponse>(`/partners/get-one`, {
            params: data
        })
        if (response.status === 200) {
            return response.data
        }
    }
)
