import {Layout} from "../../ui/layout/layout";
import {DNCBalance} from './DNCBalance/DNCBalance';
import {NumberOfPartners} from './NumberOfPartners/NumberOfPartners';
import {ReferralProfit} from './ReferralProfit/ReferralProfit';
import {NewsList} from './News/NewsList/NewsList';
import {News} from '../news/news';
import DNCBalanceIcon from './icons/DNCBalance.svg';
import RZMBalanceIcon from './icons/RZMBalance.svg';
import {ButtonsGroup} from "ui/buttons/buttonsGroup/buttonsGroup";
import {useDispatch, useSelector} from "react-redux";
import {AppStateType} from "../../core/redux/store";
import {useEffect, useState} from "react";
import {getUserDataThunk} from "modules/user/getUserData/getUserDataThunk";
import {articlesThunk} from 'modules/dashboard/articles/articlesThunk';
import notebookIcon from './icons/notebook.svg';
import {TotalContracts} from "./TotalContracts/TotalContractsSample/TotalContracts";
import {structurePartnersThunk} from "modules/structure/partners/structurePartnersThunk";
import {getInheritancesListThunk} from "modules/user/getInheritancesList/getInheritancesListThunk";
import {getNotificationThunk} from "modules/user/notification/getNotification/getNotificationThunk";
import {Popup} from "ui/popup/popup";
import styles from './Dashboard.module.scss';
import {BuyFirstPopup} from "./popups/buyFirstPopup/buyFirstPopup";
import {BuyCoins} from "ui/popup/buyCoins/BuyCoins";
import {WarningPopup} from "./popups/warningPopup/warningPopup";
import { Withdraw } from "ui/popup/withdraw/withdraw";
import { PaymentMethod } from "ui/popup/PaymentMethod/PaymentMethod";


export const Dashboard = (): JSX.Element => {
    const {
        balance, rzmBalance, totalContracts, amountOfContract
    } = useSelector((state: AppStateType) => state.user.user);
    const {articles} = useSelector((state: AppStateType) => state.articles);
    const {partners} = useSelector((state: AppStateType) => state.structurePartners);
    const {item} = useSelector((state: AppStateType) => state.getInheritancesList.inheritances);
    const [chosenNews, setChosenNews] = useState<number>(0);
    const [isNewsActive, setIsNewsActive] = useState(false);
    const [activePopup, setActivePopup] = useState(false)
    const [activeFirstPopup, setActiveFirstPopup] = useState(false)
    const [activeWarningPopup, setActiveWarningPopup] = useState(false)
    const [withdrawDnc, setWithdrawDnc] = useState(false)
    const [withdrawRzm, setWithdrawRzm] = useState(false)
    const [byDnc, setByDnc] = useState(false)
    const [byRzm, setByRzm] = useState(false)
    const [paymentMethod, setPaymentMethod] = useState(false)
    const [whatCoinBuying, setWhatCoinBuying] = useState<'DNC' | 'RZM'>('DNC')


    const dispatch = useDispatch()

    useEffect(() => {
        dispatch(getUserDataThunk());
        dispatch(structurePartnersThunk({limit: 100, offset: 1}))
        dispatch(articlesThunk());
        dispatch(getInheritancesListThunk())
        dispatch(getNotificationThunk({active: true, offset: 1, limit: 1}))
    }, [dispatch])

    useEffect(() => {
        if (item) {
            setActivePopup(prev => !prev)
        }
    }, [item])

    const toggleNewsBlock = () => {
        setIsNewsActive(!isNewsActive);
    }

    const chooseNews = (id: number) => {
        setChosenNews(id);
        toggleNewsBlock()
    }

    const DNC = {
        by: (value: boolean) => setByDnc(value),
        takeOut: (value: boolean) => setWithdrawDnc(value),
    }

    const RZM = {
        by: (value: boolean) => setByRzm(value),
        takeOut: (value: boolean) => setWithdrawRzm(value),
    }

    const showFirstPopup = () => {
        setActiveFirstPopup(prev => !prev)
    }

    const showWarningPopup = () => {
        setActiveWarningPopup(prev => !prev)
    }

    const nextPopup = (value: 'policy1' | 'policy2') => {
        setActiveFirstPopup(false)

        if (value === 'policy2') {
            DNC.by(true)
        } else {
            setActiveWarningPopup(true)
        }
    }

    const toggleCoinBuying = () => {
      if (byDnc) {
        setWhatCoinBuying('DNC');
        setByDnc(false)
      }

      if (byRzm) {
        setWhatCoinBuying('RZM');
        setByRzm(false)
      }
    }

    const showPaymentMethodPopup = () => {
      setPaymentMethod(true);
      toggleCoinBuying();
      console.log(`whatCoinBuying = ${whatCoinBuying}`);
      
    }

    return (
        <Layout>
            {!isNewsActive && (
                <div className={styles.dashboard}>
                    <div className={styles.PanelBlock}>
                        <div className={styles.balance}>
                            <DNCBalance balance={balance} title={'Баланс DNC'} income={0} icon={DNCBalanceIcon}>
                                <ButtonsGroup onClickToSaleButton={showFirstPopup}
                                              onClickToShowButton={() => DNC.takeOut(true)}/>
                            </DNCBalance>
                            <DNCBalance balance={rzmBalance} title={'Баланс RZM'} income={124} icon={RZMBalanceIcon}>
                                <ButtonsGroup onClickToSaleButton={showFirstPopup}
                                              onClickToShowButton={() => RZM.takeOut(true)}/>
                            </DNCBalance>
                        </div>
                        <div className={styles.otherBlocks}>
                            <div className={styles.contracts}>
                                <TotalContracts text='Общая сумма контрактов' sum={amountOfContract}
                                                icon={notebookIcon}/>
                                <TotalContracts text={'Всего контрактов'} sum={totalContracts} icon={notebookIcon}/>
                            </div>
                            <div className={styles.other}>
                                <NumberOfPartners partners={partners.users}/>
                                <ReferralProfit/>
                            </div>
                        </div>
                    </div>
                    <div className={styles.news}>
                        <NewsList articles={articles} onClick={chooseNews} title={'Новости'} isButton='yes'/>
                    </div>
                </div>)}

            {isNewsActive && (
                <News
                    articles={articles}
                    chosenNews={chosenNews}
                    onClick={toggleNewsBlock}
                />)}
            {activePopup && (<Popup>Показать попап с заявкой на наследство</Popup>)}

            {activeFirstPopup && (<Popup>
                <BuyFirstPopup nextStep={nextPopup} closePopup={showFirstPopup}/>
            </Popup>)}

            {activeWarningPopup && (<Popup>
                <WarningPopup nextStep={showWarningPopup} closePopup={showWarningPopup}/>
            </Popup>)}

            {byDnc && (<Popup>
                <BuyCoins
                  closePopup={() => DNC.by(false)}
                  icon={DNCBalanceIcon}
                  nextStep={() => showPaymentMethodPopup}
                  title={'DNC'}
                />
            </Popup>)}

            {withdrawDnc && (<Popup>
              <Withdraw
                closePopup={() => DNC.takeOut(false)}
                icon={DNCBalanceIcon}
                nextStep={() => {}}
                title={'DNC'}
              />
            </Popup>)}

            {byRzm && (<Popup>
                <BuyCoins
                  closePopup={() => RZM.by(false)}
                  icon={RZMBalanceIcon}
                  nextStep={() => showPaymentMethodPopup}
                  title={'RZM'}
                />
            </Popup>)}
            {withdrawRzm && (<Popup>
              <Withdraw
                closePopup={() => RZM.takeOut(false)}
                icon={RZMBalanceIcon}
                nextStep={() => {}}
                title={'RZM'}
              />
            </Popup>)}
            {paymentMethod && (<Popup>
              <PaymentMethod nextStep={() => { } } closePopup={() => setPaymentMethod(false)} coinName={whatCoinBuying}/>
            </Popup>)}
        </Layout>
    )
}