import {createAsyncThunk} from '@reduxjs/toolkit';
import {baseRequestAxios} from 'api/baseRequest';
import {NextLevelBlock} from "api/type/structure";

export const nextLevelBlock = createAsyncThunk(
    'nextLevelBlock',
    async () => {
        const response = await baseRequestAxios.get<NextLevelBlock>("/api/structure/next-level-block")
        if (response.status === 200) {
            return response.data
        }
    }
)
