import {createSlice} from '@reduxjs/toolkit';
import {updateUserPasswordThunk} from './updateUserPasswordThunk';

const updateUserPasswordSlice = createSlice({
    name: 'updateUserPassword',
    initialState: {
        old_password: '',
        new_password: '',
        success: false,
        loading: false,
        error: false
    },
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(updateUserPasswordThunk.pending, (state) => {
            state.loading = true
        });
        builder.addCase(updateUserPasswordThunk.fulfilled, (state, action) => {
            if (action.payload) {
                state.success = action.payload.success;
                state.loading = false
            }
        });
        builder.addCase(updateUserPasswordThunk.rejected, (state) => {
            state.loading = false;
            state.error = true
        });
    },
});

export const updateUserPasswordReducer = updateUserPasswordSlice.reducer;