import styles from './NewsItem.module.scss';

import { ReactSVG } from 'react-svg';
import eyeIcon from '../../icons/newsEye.svg';
import calendarIcon from '../../icons/newsCalendar.svg';
import { Article } from 'api/type/articles';
import newsImage from '../../icons/newsImage.png'

interface Props {
  article: Article;
  onClick: (id: number) => void;
}

export const NewsItem = ({ article, onClick}: Props): JSX.Element => {
  const { image, ruTitle, ruShortDescription, id, createdAt} = article;
  const baseUrl = process.env.REACT_APP_API_URL
  const articleImage = `${baseUrl}${image}`;

  return (
    <div className={styles.newsItem} onClick={() => onClick(id)}>
      <div className={styles.image}>
        {image? (<img src={articleImage} alt="NewsImage" />): (<img src={newsImage} alt="NewsImage" />)}
      </div>
      <div className={styles.text}>
        <h2 className={styles.title}>{ruTitle}</h2>
        <p className={styles.textNews}>{ruShortDescription}...</p>
      </div>
      <div className={styles.info}>
        <ReactSVG className={styles.eyeIcon} src={eyeIcon} />
        <span>{'128'}</span>
        <ReactSVG className={styles.calendarIcon} src={calendarIcon} />
        <span>{createdAt}</span>
      </div>
    </div>
  )
}
