import {createSlice} from '@reduxjs/toolkit';
import {update_InheritanceThunk} from './update_InheritanceThunk';

const update_InheritanceSlice = createSlice({
    name: 'update_Inheritance',
    initialState: {
        success: false,
        loading: false,
        error: false
    },
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(update_InheritanceThunk.pending, (state) => {
            state.loading = true;
        });
        builder.addCase(update_InheritanceThunk.fulfilled, (state, action) => {
            if (action.payload) {
                state.success = action.payload.success;
                state.loading = false
            }
        });
        builder.addCase(update_InheritanceThunk.rejected, (state) => {
            state.loading = false
            state.error = true
        });
    },

});

export const update_InheritanceReducer = update_InheritanceSlice.reducer;