import {createAsyncThunk} from '@reduxjs/toolkit';
import {baseRequestAxios} from 'api/baseRequest';
import {StructureBonusesResponse} from "api/type/structure";

export const structureBonuses = createAsyncThunk(
    'structureBonuses',
    async () => {
        const response = await baseRequestAxios.get<StructureBonusesResponse>("/api/structure/bonuses")
        if (response.status === 200) {
            return response.data
        }
    }
)
