import {ReactSVG} from "react-svg";
import {useState} from "react";
import style from "./swichButton.module.scss";

interface Props {
    icon: string,
    active: boolean,
    onChange?: (value: boolean) => void
}

export const SwitchButton = ({icon, onChange, active}: Props): JSX.Element => {
    const [activeButton, setActive] = useState(active)

    const switchAction = () => {
        setActive(prev => !prev)

        if (onChange) {
            onChange(activeButton)
        }
    }

    return (
        <div className={`${style.switcherWrapper} ${activeButton ? style.active : ''}`}>
            <div className={style.button} onClick={switchAction}>
                <ReactSVG src={icon} className={`${style.icon}`}/>
            </div>
        </div>
    )
}
