import {createAsyncThunk} from '@reduxjs/toolkit';
import {baseRequestAxios} from 'api/baseRequest';
import {eventsRequest, eventsResponse} from 'api/type/events';

export const eventsThunk = createAsyncThunk(
    'events',
    async (requestData: eventsRequest) => {

        const response = await baseRequestAxios.get<eventsResponse>("/api/event/list", {
            params: requestData,
        })
        if (response.status === 200) {
            return response.data
        }

    }
)
