import {createAsyncThunk} from '@reduxjs/toolkit';
import {baseRequestAxios} from 'api/baseRequest';
import {ContractsResponse, ContractsRequest} from 'api/type/contracts/contracts';

export const contractsThunk = createAsyncThunk(
    'contracts',
    async (requestData: ContractsRequest) => {
        const response = await baseRequestAxios.get<ContractsResponse>("/api/contract", {
            params: requestData,
        })
        if (response.status === 200) {
            return response.data
        }
    }
)
