import {createAsyncThunk} from '@reduxjs/toolkit';
import {baseRequestAxios} from 'api/baseRequest';

export const sendSmsCode = createAsyncThunk(
    'sendSmsCode',
    async (phone: string) => {
        const response = await baseRequestAxios.post<{id: number}>("/registration/send-sms-code", {
            phone: phone
        })
        if (response.status === 200) {
            return response.data
        }
    }
)
