import {createSlice} from '@reduxjs/toolkit';
import {updateUserAvatarThunk} from './updateUserAvatarThunk';

const updateUserAvatarSlice = createSlice({
    name: 'updateUserAvatar',
    initialState: {
        updateAvatarSuccess: false,
        loading: false,
        error: false
    },
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(updateUserAvatarThunk.pending, (state) => {
            state.loading = true
        });
        builder.addCase(updateUserAvatarThunk.fulfilled, (state, action) => {
            if (action.payload) {
                state.updateAvatarSuccess = action.payload.success;
                state.loading = false
            }
        });
        builder.addCase(updateUserAvatarThunk.rejected, (state) => {
            state.loading = false;
            state.error = true
        });
    },
});

export const updateUserAvatarReducer = updateUserAvatarSlice.reducer;