import {createAsyncThunk} from '@reduxjs/toolkit';
import {baseRequestAxios} from 'api/baseRequest';
import {Update_InheritanceRequest} from "api/type/inheritance";

export const update_InheritanceThunk = createAsyncThunk(
    'update_Inheritance',
    async (requestData: Update_InheritanceRequest) => {
        const response = await baseRequestAxios.post<{success: boolean}>("/api/user/heir/update", {
            params: requestData
        })
        if (response.status === 200) {
            return response.data
        }
    }
)

