import {useEffect, useState} from "react";
import {ReactSVG} from "react-svg";
import {AppStateType} from "../../core/redux/store";
import {Route, Switch} from "react-router-dom";
import {useDispatch, useSelector} from "react-redux";
import {Layout} from "ui/layout/layout"
import {SettingsMenu} from "./components/settingsMenu/settingsMenu";
import {RoutesType} from "../routes";
import {Documents} from "./components/pages/documents/documents";
import {Inheritance} from "./components/pages/inheritance/inheritance";
import {Notifications} from "./components/pages/notifications/notifications";
import {PersonalData} from "./components/pages/personalData/personalData";
import {Security} from "./components/pages/security/security";
import {SocialNetwork} from "./components/pages/socialNetwork/socialNetwork";
import {updateUserDataThunk} from "modules/user/updateUserData/updateUserDataThunk";
import iconUser from './icon/iconUser.svg'
import style from './settings.module.scss'

const settingsRoutes: RoutesType[] = [
    {name: 'personalData', path: `/settings/personal-data/`, exact: true, component: <PersonalData/>},
    {name: 'security', path: `/settings/security/`, exact: true, component: <Security/>},
    {name: 'socialNetwork', path: `/settings/social-network/`, exact: true, component: <SocialNetwork/>},
    {name: 'notifications', path: `/settings/notifications/`, exact: true, component: <Notifications/>},
    {name: 'documents', path: `/settings/documents/`, exact: true, component: <Documents/>},
    {name: 'inheritance', path: `/settings/inheritance/`, exact: true, component: <Inheritance/>},
];

export const Settings = (): JSX.Element => {
    const {user, isChangedData} = useSelector((state: AppStateType) => state.user)
    const [selectedItemMenu, setSelectedItemMenu] = useState<{ value: string, icon: string }>({
        value: 'Личные данные',
        icon: iconUser
    })
    const [showMenu, setShowMenu] = useState(true)
    const dispatch = useDispatch()

    useEffect(() => {
        if (isChangedData) {
            dispatch(updateUserDataThunk(user))
        }
    }, [dispatch, isChangedData, user])

    const changeItemMenu = (data: { value: string, icon: string }) => {
        setSelectedItemMenu(data)
        toggleShowMenu()
    }

    const toggleShowMenu = () => {
        setShowMenu(prev => !prev)
    }

    return (
        <Layout>
            <div className={style.settings}>
                <div className={`${style.showMenu} ${!showMenu ? style.hiddenMenu : ''}`}>
                    <SettingsMenu onSelectItemMenu={changeItemMenu}/>
                </div>
                <div className={style.settings_leftBlock}>
                    <div className={style.topBlock}>
                        <span className={style.selectedValue}>{selectedItemMenu.value}</span>
                        <div className={style.mobileMenu}>
                            <div onClick={toggleShowMenu} className={style.mobileMenu_block}>
                                <ReactSVG src={selectedItemMenu.icon}/>
                            </div>
                        </div>
                    </div>
                    <div className={style.settings_wrapper}>
                        <Switch>
                            {settingsRoutes.map(item =>
                                (<Route key={item.name} path={item.path} render={() => item.component}/>)
                            )}
                        </Switch>
                    </div>
                </div>
            </div>
        </Layout>
    )
}