import {useForm} from "react-hook-form";
import {FormItem} from "../../../formItem/formItem";
import {InputPassword} from "ui/inputs/inputPassword/inputPassword";
import {TakeOutButton} from "ui/buttons/TakeOutButton/TakeOutButton";
import {ChangePasswordFormProps} from "api/type/settings";
import {UpdateUserPasswordRequest} from "api/type/user/user";
import {ErrorsMessage} from "ui/errorsMessage/errorsMessage";
import {useState} from "react";

interface PropsFormPasswordForAccount {
    onChangePassword: (data: UpdateUserPasswordRequest) => void;
    error: boolean;
}

export const FormPassword = ({onChangePassword, error}: PropsFormPasswordForAccount): JSX.Element => {
    const [errorMatchingPasswords, setErrorMatchingPasswords] = useState(false)
    const {handleSubmit, control, formState: {errors}} = useForm<ChangePasswordFormProps>({
        defaultValues: {
            old_password: '',
            new_password: '',
            repeatPassword: ''
        },
        mode: 'onChange',
    });

    const onSubmit = ({new_password, old_password, repeatPassword}: ChangePasswordFormProps) => {
        if (new_password === repeatPassword) {
            onChangePassword({new_password, old_password})
            setErrorMatchingPasswords(false)
        } else {
            setErrorMatchingPasswords(true)
        }
    }

    return (
        <form onSubmit={handleSubmit(onSubmit)}>
            <FormItem name='password' label='Старый пароль'>
                <InputPassword control={control} name={'old_password'} rules={{required: true}}/>
            </FormItem>
            {errors.old_password && (<ErrorsMessage textMessage={'Это поле обязательное'}/>)}

            <FormItem name='newPassword' label='Новый пароль'>
                <InputPassword control={control} name={'new_password'} rules={{required: true}}/>
            </FormItem>
            {errors.new_password && (<ErrorsMessage textMessage={'Это поле обязательное'}/>)}

            <FormItem name='repeatPassword' label='Подтвердите пароль'>
                <InputPassword control={control} name={'repeatPassword'} rules={{required: true}}/>
            </FormItem>
            {errors.repeatPassword && (<ErrorsMessage textMessage={'Это поле обязательное'}/>)}
            {errorMatchingPasswords && (<ErrorsMessage textMessage={'Пароли не совпадают'}/>)}
            {error && (<ErrorsMessage textMessage={'Ошибка смены пароля'}/>)}

            <TakeOutButton color={'blue'}>
                сохранить
            </TakeOutButton>
        </form>
    )
}