import {createSlice} from '@reduxjs/toolkit';
import {readNotificationThunk} from './readNotificationThunk';
import {ReadNotificationResponse} from "api/type/user/readNotification";

const readNotificationSlice = createSlice({
    name: 'readNotification',
    initialState: {
        notification: {} as ReadNotificationResponse,
        loading: false,
        error: false,
    },
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(readNotificationThunk.pending, (state) => {
            state.loading = true
        });
        builder.addCase(readNotificationThunk.fulfilled, (state, action) => {
            if (action.payload) {
                state.notification = action.payload
                state.loading = false;
            }
        });
        builder.addCase(readNotificationThunk.rejected, (state) => {
            state.loading = false;
            state.error = true
        });
    },
});

export const readNotificationReducer = readNotificationSlice.reducer;