import BuyButton from '../BuyButton/BuyButton'
import {TakeOutButton} from '../TakeOutButton/TakeOutButton'
import styles from './buttonsGroup.module.scss'

interface Props {
    onClickToSaleButton?: () => void,
    onClickToShowButton?: () => void,
}

export const ButtonsGroup = ({onClickToSaleButton, onClickToShowButton}: Props): JSX.Element => {
    return (
        <div className={styles.buttons}>
            <div className={styles.firstButton}>
                <BuyButton color={'green'} onClick={onClickToSaleButton!}>Купить</BuyButton>
            </div>
            <div className={styles.secondButton}>
                <TakeOutButton color={'green'} onClick={onClickToShowButton}>Вывести</TakeOutButton>
            </div>
        </div>
    )
}