import {createAsyncThunk} from '@reduxjs/toolkit';
import {baseRequestAxios} from 'api/baseRequest';
import {ReadNotificationRequest, ReadNotificationResponse} from "api/type/user/readNotification";

export const readNotificationThunk = createAsyncThunk(
    'readNotification',
    async (data: ReadNotificationRequest) => {
        const response = await baseRequestAxios.get<ReadNotificationResponse>("/api/notification/read", {
            params: data
        })
        if (response.status === 200) {
            return response.data;
        }
    }
)
