import {useEffect, useRef, useState} from 'react';
import {useDispatch, useSelector} from "react-redux";
import {AppStateType} from "core/redux/store";
import {PersonalDataForm} from "./personalDataForm";
import {TakeOutButton} from "ui/buttons/TakeOutButton/TakeOutButton";
import {PersonalDataFormProps} from "api/type/user/user";
import {upDatePersonalData} from "modules/user/getUserData/getUserDataSlice";
import {ShowAvatar} from "ui/showAvatar/showAvatar";
import {updateUserAvatarThunk} from "modules/user/updateUserAvatar/updateUserAvatarThunk";
import style from './personalData.module.scss'

export const PersonalData = (): JSX.Element => {
    const {avatar, firstName, lastName, isVerify} = useSelector((state: AppStateType) => state.user.user)
    const [file, setFile] = useState<File | null>(null)
    const ref = useRef<HTMLInputElement | null>(null)
    const dispatch = useDispatch()

    const editData = (data: PersonalDataFormProps) => {
        dispatch(upDatePersonalData(data))
    }

    const goVerification = () => {}

    useEffect(() => {
        if (file) {
            const formData = new FormData()
            formData.append('avatar', file)

            dispatch(updateUserAvatarThunk({avatar: formData}))
        }
    }, [dispatch, file])

    const editAvatar = (file: File) => setFile(file)

    const deleteAvatar = () => {
    }

    const avatarChange = () => {
        if (ref.current) {
            ref.current.click()
        }
    }

    return (
        <div className={style.personalData}>
            <div className={style.personalData_aboutMe}>
                <div className={style.aboutMe_data}>
                    <div className={style.myAvatar}>
                        <ShowAvatar avatar={avatar}/>
                        <div className={style.myAvatar_settings}>
                            <input ref={ref} type="file" onChange={(e) => editAvatar(e.target.files![0])}/>

                            <TakeOutButton onClick={avatarChange} color={'blue'}>Изменить</TakeOutButton>
                            <TakeOutButton color={'blue'} onClick={deleteAvatar}>Удалить</TakeOutButton>
                        </div>
                    </div>
                    <div className={style.myData}>
                        <div className={`${style.myName}`}>
                            <span className={style.title}>Имя</span>
                            <span className={style.description}>{firstName}</span>
                        </div>
                        <div className={`${style.myName}`}>
                            <span className={style.title}>Фамилия</span>
                            <span className={style.description}>{lastName}</span>
                        </div>
                    </div>
                </div>
                <div className={style.aboutMe_contacts}>
                    <PersonalDataForm onSubmitHandle={editData}/>
                </div>
            </div>
            <div className={style.personalData_verification}>
                <span className={style.title}>Верификация</span>
                    {isVerify === 2 && (
                      <p className={style.description}>Аккаунт верифицирован!</p>
                    )}
                    {isVerify === 0 && (
                      <>
                        <p className={style.description}>Чтобы пользоваться всем функционалом платформы, нужно пройти быструю
                            верификайию всего в 3 шага!</p>
                        <TakeOutButton color={'blue'} onClick={goVerification}>
                            Пройти верификацию
                        </TakeOutButton>
                      </>
                    )}
            </div>
        </div>
    )
}