import {useState} from "react";
import {ClosePopup} from "ui/buttons/closepPopup/closePopup";
import {InputRadio} from "ui/inputs/inputRadio/inputRadio";
import BuyButton from "ui/buttons/BuyButton/BuyButton";
import style from './buyFirstPopup.module.scss'

type Policy = 'policy1' | 'policy2'

interface Props {
    closePopup: () => void;
    nextStep: (value: Policy) => void
}

export const BuyFirstPopup = ({closePopup, nextStep}: Props): JSX.Element => {
    const [selectPolicy, setSelectPolicy] = useState<Policy>('policy1')
    const [checked, setChecked] = useState(false)

    const selectedPolicy = (value: Policy) => {
        setSelectPolicy(value)
        setChecked(prev => !prev)
    }

    return (
        <div className={style.byWarningPopup}>
            <ClosePopup onClose={closePopup}/>
            <h3>Приобретение</h3>
            <p>Подтвердите источник средств, которые вы планируете использовать в работе
                с<span> ManGo.Trade</span></p>

            <div className={style.selectOption}>
                <InputRadio<Policy> value={'policy1'} checked={!checked} name={'policy1'}
                                    onChange={selectedPolicy}/>
                <label htmlFor='policy1'>Я довожу до сведений, что собираюсь использовать заемные или кредитные
                    средства.</label>
            </div>
            <div className={style.selectOption}>
                <InputRadio<Policy> value={'policy2'} name={'policy2'} checked={checked}
                                    onChange={selectedPolicy}/>
                <label htmlFor="policy2">Я подтверждаю, что собираюсь использовать свой личный капитал.</label>
            </div>
            <div className={style.buttonNext}>
                <BuyButton color={'green'} onClick={() => nextStep(selectPolicy)}>Далее</BuyButton>
            </div>
        </div>
    )
}