import {createAsyncThunk} from '@reduxjs/toolkit';
import {baseRequestAxios} from 'api/baseRequest';
import {UpdateUserPasswordRequest} from "api/type/user/user";

export const updateUserPasswordThunk = createAsyncThunk(
    'updateUserPassword',
    async ({new_password, old_password}: UpdateUserPasswordRequest) => {
        const response = await baseRequestAxios.post<{ success: true }>("/api/user/password", {
            old_password: old_password,
            new_password: new_password
        })
        if (response.status === 200) {
            return response.data;
        }
    }
)
