import {createSlice} from '@reduxjs/toolkit';
import {getGlobalStatisticsThunk} from './getGobalStatisticsThunk';
import {GetGlobalStatisticsResponse} from "api/type/statistics/getGlobalStatistics";

const getGlobalStatisticsSlice = createSlice({
    name: 'getGlobalStatistics',
    initialState: {
        globalStatistics: {} as GetGlobalStatisticsResponse,
        loading: false,
        error: false,
        isChangedData: false
    },
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(getGlobalStatisticsThunk.pending, (state) => {
            state.loading = true
        });
        builder.addCase(getGlobalStatisticsThunk.fulfilled, (state, action) => {
            if (action.payload) {
                state.loading = false;
                state.isChangedData = false
            }
        });
        builder.addCase(getGlobalStatisticsThunk.rejected, (state) => {
            state.loading = false;
            state.error = true
        });
    },
});

export const getGlobalStatisticsReducer = getGlobalStatisticsSlice.reducer;