import {createSlice} from '@reduxjs/toolkit';
import {updateTimeSessionThunk} from './updateTimeSessionThunk';

const updateTimeSessionSlice = createSlice({
    name: 'updateTimeSession',
    initialState: {
        success: false,
        loading: false,
        error: false
    },
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(updateTimeSessionThunk.pending, (state) => {
            state.loading = true
        });
        builder.addCase(updateTimeSessionThunk.fulfilled, (state, action) => {
            if (action.payload) {
                state.success = action.payload.success
                state.loading = false
            }
        });
        builder.addCase(updateTimeSessionThunk.rejected, (state) => {
            state.loading = false;
            state.error = true
        });
    },
});

export const updateTimeSessionReducer = updateTimeSessionSlice.reducer;