import {ReactSVG} from 'react-svg';
import {useEffect, useState} from "react";
import styles from './TotalContracts.module.scss';

type Props = {
    text?: string;
    sum: number;
    icon: string;
}

export const TotalContracts = (props: Props) => {
    const {icon, sum, text} = props
    const [result, setResult] = useState(sum)

    useEffect( () => {
        if (sum > 0) {
            setResult(Math.floor(sum * 100) / 100)
        } else {
            setResult(sum)
        }
    }, [sum] )

    return (
        <div className={styles.wrapper}>
            <div className={styles.TotalContracts}>
                <div className={styles.iconBox}>
                    <ReactSVG className={styles.notebookIcon} src={icon}/>
                </div>
                <div className={styles.sum}>
                    {result}
                </div>
                <div className={styles.text}>
                    <span>{text}</span>
                </div>
            </div>
        </div>
    )
}
