import {createAsyncThunk} from '@reduxjs/toolkit';
import {baseRequestAxios} from 'api/baseRequest';
import {GetGlobalStatisticsRequest, GetGlobalStatisticsResponse} from "api/type/statistics/getGlobalStatistics";

export const getGlobalStatisticsThunk = createAsyncThunk(
    'getGlobalStatistics',
    async (dataL: GetGlobalStatisticsRequest) => {
        const response = await baseRequestAxios.get<GetGlobalStatisticsResponse>("/api/global-stat")
        if (response.status === 200) {
            return response.data;
        }
    }
)
