import {createAsyncThunk} from '@reduxjs/toolkit';
import {baseRequestAxios} from 'api/baseRequest';
import {OneNewsRequest, OneNewsResponse} from 'api/type/oneNews';

export const oneNewsThunk = createAsyncThunk(
    'oneNews',
    async (requestData: OneNewsRequest) => {
        const response = await baseRequestAxios.get<OneNewsResponse>("/news/get-one", {
            params: requestData,
        })
        if (response.status === 200) {
            return response.data
        }
    }
)
