import {useState} from "react";
import { ReactSVG } from "react-svg";
import plusIcon from '../../pages/contacts/icons/plusBlock.svg';
import minusIcon from '../../pages/contacts/icons/minusBlock.svg';
import styles from './counter.module.scss'

export const Counter = (): JSX.Element => {
  const [value, setValue] = useState(0);
  const changeAmount = (type: string) => {
    if (type === 'minus') {
        if (value > 1) {
          setValue(value - 1)
        }
    }
    if (type === 'plus') {
      setValue(value + 1)
    }
  }
    const handleChange = (value: string) => {
      setValue(Number(value));
    }

    return (
        <div className={styles.count}>
            <div className={styles.minusBlock}>
                <ReactSVG onClick={() => changeAmount('minus')} className={styles.countIcon} src={minusIcon}/>
            </div>
              <input type="text" value={value} onChange={(event) => handleChange(event.target.value)} />
            <div className={styles.plusBlock}>
                <ReactSVG onClick={() => changeAmount('plus')} className={styles.countIcon} src={plusIcon}/>
            </div>
        </div>
    )
}