import {ReactSVG} from 'react-svg';
import styles from './NumberOfPartners.module.scss';
import partnersIcon from '../icons/partners.svg';
import {ShowAvatar} from "../../../ui/showAvatar/showAvatar";

interface Props {
    partners: { id: number, image?: string | null, avatar?: string | null }[]
}

export const NumberOfPartners = ({partners}: Props): JSX.Element => {
    return (
      <>
        {partners && (
          <div className={styles.wrapper}>
              <div className={styles.NumberOfPartners}>
                  <div className={styles.iconBox}>
                      <ReactSVG className={styles.partnersIcon} src={partnersIcon}/>
                  </div>
                  <div className={styles.sum}>
                      <div className={styles.partnersPhoto}>
                          {partners.map((partner, index) => (
                              <div key={partner.id} >
                                  {index < 6 &&
                                  <div className={styles.partnerPhoto}>
                                      <ShowAvatar avatar={partner.image || partner.avatar} />
                                  </div>}
                              </div>
                          ))}
                      </div>
                      <span className={styles.partnersCount}>{partners.length}</span>
                  </div>
                  <div className={styles.text}>
                      Общее количество партнеров
                  </div>
              </div>
          </div>
        )}
      </>
    )
}
