import {createSlice} from '@reduxjs/toolkit';
import {updateUserFinPasswordThunk} from './updateUserFinPasswordThunk';

const updateUserFinPasswordSlice = createSlice({
    name: 'updateUserFinPassword',
    initialState: {
        old_password: '',
        new_password: '',
        success: false,
        loading: false,
        error: false
    },
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(updateUserFinPasswordThunk.pending, (state) => {
            state.loading = true
        });
        builder.addCase(updateUserFinPasswordThunk.fulfilled, (state, action) => {
            if (action.payload) {
                state.success = action.payload.success;
                state.loading = false
            }
        });
        builder.addCase(updateUserFinPasswordThunk.rejected, (state) => {
            state.loading = false;
            state.error = true
        });
    },
});

export const updateUserFinPasswordReducer = updateUserFinPasswordSlice.reducer;