import {useState} from "react";
import {ReactSVG} from "react-svg"
import BuyButton from '../../buttons/BuyButton/BuyButton';
import {ClosePopup} from "../../buttons/closepPopup/closePopup";
import {Counter} from "../../counter/counter";
import styles from './BuyCoins.module.scss'

interface Props {
    icon: string;
    nextStep: (coin: 'DNC' | 'RZM') => void;
    title: 'DNC' | 'RZM';
    closePopup: () => void
}

export const BuyCoins = ({icon, closePopup, title, nextStep}: Props): JSX.Element => {
    const [isFirstRulesAgree, setIsFirstRulesAgree] = useState(false);
    const [isSecondRulesAgree, setIsSecondRulesAgree] = useState(false);
    const [isThirdRulesAgree, setIsThirdRulesAgree] = useState(false);
    const isAgreeWithRules = isFirstRulesAgree && isSecondRulesAgree && isThirdRulesAgree;

    return (
        <div className={styles.buyCoins}>
                <ClosePopup onClose={closePopup}/>
            <div className={styles.coinsNameBlock}>
                Приобретение
                <div className={styles.coinsName}>
                    <ReactSVG className={styles.icon} src={icon}/>
                    {title}
                </div>
            </div>
            <div className={styles.infoBlock}>
                <p className={styles.infoAboutWallet}>
                    Чтобы добавить себе в кошелек Метамаск или Trust Wallet токены {title}, добавьте новый токен в
                    сети Smart Chain (BSC)
                </p>
                <ul className={styles.infoItemList}>
                    <li className={styles.infoItem}>
                        Символ токена {title}
                    </li>
                    <li className={styles.infoItem}>
                        Адрес контракта 0x932d7cb944168047c5dab270dc35ab0775d23e9c
                    </li>
                    <li className={styles.infoItem}>
                        Число десятичных знаков токена 8
                    </li>
                </ul>
            </div>
            <div className={styles.buyCoinsBlock}>
                <p className={styles.buyCoinsInfo}>
                    Введите сумму, которая будет зачислена на ваш баланс
                </p>
                <Counter/>
            </div>
            <div className={styles.regulations}>
                <div className={styles.regulationPart}>
                    <div className={styles.checkBoxWrapper}>
                        <input
                            className={styles.checkBox}
                            type='checkbox'
                            onClick={() => setIsFirstRulesAgree(!isFirstRulesAgree)}
                        />
                    </div>
                    <p className={styles.checkBoxText}>
                        Я согласен отправить эту сумму одним платежом
                    </p>
                </div>
                <div className={styles.regulationPart}>
                    <div className={styles.checkBoxWrapper}>
                        <input
                            className={styles.checkBox}
                            type='checkbox'
                            onClick={() => setIsSecondRulesAgree(!isSecondRulesAgree)}
                        />
                    </div>
                    <p className={styles.checkBoxText}>
                        Я согласен на комиссию в размере 1$
                    </p>
                </div>
                <div className={styles.regulationPart}>
                    <div className={styles.checkBoxWrapper}>
                        <input
                            className={styles.checkBox}
                            type='checkbox'
                            onClick={() => setIsThirdRulesAgree(!isThirdRulesAgree)}
                        />
                    </div>
                    <p className={styles.checkBoxText}>
                        Я прочитал и согласен с политикой риска
                    </p>
                </div>
            </div>
            <div className={styles.fakeInfoButton}>
                <BuyButton disabled={!isAgreeWithRules} onClick={nextStep(title)} color='blue'>
                    Далее
                </BuyButton>
            </div>
        </div>
    )
}