import {createAsyncThunk} from '@reduxjs/toolkit';
import {baseRequestAxios} from 'api/baseRequest';
import {InheritancesListFormProps} from "api/type/settings";

export const createInheritanceThunk = createAsyncThunk(
    'createInheritance',
    async (data: InheritancesListFormProps) => {
        const response = await baseRequestAxios.post<{success: boolean}>("/api/user/heir/create", {
            fio: data.fio,
            email: data.email,
            phone: data.phone,
        })
        if (response.status === 200) {
            return response.data;
        }
    }
)
