import {createSlice} from '@reduxjs/toolkit';
import {updateUserDataThunk} from './updateUserDataThunk';

const updateUserDataSlice = createSlice({
    name: 'updateUserData',
    initialState: {
        success: false,
        loading: false,
        error: false
    },
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(updateUserDataThunk.pending, (state) => {
            state.loading = true
        });
        builder.addCase(updateUserDataThunk.fulfilled, (state, action) => {
            if (action.payload) {
                state.success = action.payload.success;
                state.loading = false
            }
        });
        builder.addCase(updateUserDataThunk.rejected, (state) => {
            state.loading = false;
            state.error = true
        });
    },
});

export const updateUserDataReducer = updateUserDataSlice.reducer;