import {createSlice} from '@reduxjs/toolkit';
import {PartnerSiteResponse} from 'api/type/partnersSites';
import {partnersSiteThunk} from './partnersSiteThunk';

const partnersSitesSlice = createSlice({
    name: 'partners',
    initialState: {
        partnersSites: {} as PartnerSiteResponse,
        loading: false,
        error: false
    },
    reducers: {},
    extraReducers: (builder) => {
        builder.addCase(partnersSiteThunk.pending, (state) => {
            state.loading = true;
        });
        builder.addCase(partnersSiteThunk.fulfilled, (state, action) => {
            if (action.payload) {
                state.partnersSites = action.payload;
                state.loading = false
            }
        });
        builder.addCase(partnersSiteThunk.rejected, (state) => {
            state.loading = false
            state.error = true
        });
    },

});

export const partnersSiteReducer = partnersSitesSlice.reducer;