import { ClosePopup } from 'ui/buttons/closepPopup/closePopup';
import styles from './PaymentMethod.module.scss';
import DNCIcon from 'pages/dashboard/icons/DNCBalance.svg';
import RZMIcon from 'pages/dashboard/icons/DNCBalance.svg';
import BTCIcon from './BTC.svg';
import { ReactSVG } from 'react-svg';
import BuyButton from 'ui/buttons/BuyButton/BuyButton';

interface Props {
  nextStep: () => void;
  closePopup: () => void;
  coinName: 'DNC' | 'RZM';
}

const paymentMethodItems = [
  {
    name: 'BTC',
    icon: BTCIcon,
  },
  {
    name: 'Банковская карта',
  },
  {
    name: 'DNC',
    icon: DNCIcon,
  },
  {
    name: 'RZM',
    icon: RZMIcon,
  },
]

export const PaymentMethod = ({closePopup = () => {}, nextStep, coinName}: Props): JSX.Element => {
  const anotherCoin = coinName === 'DNC' ? 'DNC' : 'RZM';
  console.log(coinName);
  

  return (
    <div className={styles.PaymentMethodPage}>
        <ClosePopup onClose={closePopup}/>
        <h2 className={styles.title}>Способ оплаты</h2>
        <div className={styles.PaymentMethodList}>
        {paymentMethodItems.map(item => (
          <div className={`${styles.PaymentMethodItem} ${(item.name === anotherCoin) ? styles.active : ''}`} key={item.name}>
            {item.icon && (
              <div className={styles.icon}>
                <ReactSVG className={styles.icon} src={item.icon}/>
              </div>)}
            <div className={styles.name}>
              {item.name}
            </div>
          </div>
        ))}
        </div>
        <div className={styles.button}>
          <BuyButton disabled={true} onClick={() => {}} color='blue'>
              Далее
          </BuyButton>
          </div>
    </div>
  )
}
