import {createAsyncThunk} from '@reduxjs/toolkit';
import {baseRequestAxios} from 'api/baseRequest';
import {VerificationFirstStepRequest} from "api/type/verification/verificationFirstStep";

export const verificationFirstStepThunk = createAsyncThunk(
    'verificationFirstStep',
    async (requestData: VerificationFirstStepRequest) => {
        const response = await baseRequestAxios.post("/api/user/verification-first-step", {
            country: requestData.country,
            first: requestData.first,
            last: requestData.last,
            middle: requestData.middle,
            gender: requestData.gender,
            date: requestData.date,
        })
        if (response.status === 200) {
            return response.data
        }
    }
)

