import {SwitchButton} from "ui/buttons/swichButton/switchButton";
import switchIcon from '../../../../icon/iconSwitcher.svg'
import style from '../security.module.scss'

interface Props {
    onChange?: (value: boolean) => void,
    active: boolean,
}

export const AuthenticationToggle = ({onChange, active}: Props): JSX.Element => {
    return (
        <div className={style.authentication_toggleWrapper}>
        <SwitchButton active={active} onChange={onChange} icon={switchIcon} />
            <span>Включить</span>
        </div>
    )
}