import {useEffect} from "react";
import {useDispatch, useSelector} from "react-redux";
import {useForm} from "react-hook-form";
import {FormItem} from "../../formItem/formItem";
import {Input} from "ui/inputs/input/input";
import {TakeOutButton} from "ui/buttons/TakeOutButton/TakeOutButton";
import {AppStateType} from "core/redux/store";
import {getInheritancesListThunk} from "modules/user/getInheritancesList/getInheritancesListThunk";
import {InheritancesListFormProps} from "api/type/settings";
import {createInheritanceThunk} from "modules/user/createInheritance/createInheritanceThunk";
import {ErrorsMessage} from "ui/errorsMessage/errorsMessage";
import {update_InheritanceThunk} from "modules/user/update_Inheritance/update_InheritanceThunk";
import {DeleteContact} from "ui/buttons/deleteContact/deleteContact";
import style from './inheritance.module.scss'

export const Inheritance = (): JSX.Element => {
    const {error} = useSelector((state: AppStateType) => state.createInheritance)
    const {item} = useSelector((state: AppStateType) => state.getInheritancesList.inheritances)
    const dispatch = useDispatch()

    const {handleSubmit, control, formState: {errors}} = useForm<InheritancesListFormProps>({
        defaultValues: {
            email: '',
            phone: '',
            fio: '',
        },
        mode: 'onChange',
    });

    useEffect(() => {
        dispatch(getInheritancesListThunk())
    }, [dispatch])

    const onSubmit = (data: InheritancesListFormProps) => {
        dispatch(createInheritanceThunk(data))
    }

    const deleteInheritance = (id: number) => {
        dispatch(update_InheritanceThunk({state: 3, id: id}))
    }

    return (
        <div className={style.inheritance}>
            <div className={style.inheritance_itemBlock}>
                <h4>Указать наследника</h4>
                <form className={style.form} onSubmit={handleSubmit(onSubmit)}>
                    <div className={style.formItem_wrapper}>
                        <FormItem name={'email'} label='Email'>
                            <Input<InheritancesListFormProps> control={control} name={'email'}
                                                              rules={{required: true}}/>
                        </FormItem>
                        {errors.email && (<ErrorsMessage textMessage={'Это поле обязательное'}/>)}
                    </div>

                    <div className={style.formItem_wrapper}>
                        <FormItem name={'phone'} label='Телефон'>
                            <Input<InheritancesListFormProps> control={control} name={'phone'}
                                                              rules={{required: true}}/>
                        </FormItem>
                        {errors.phone && (<ErrorsMessage textMessage={'Это поле обязательное'}/>)}
                    </div>

                    <div className={style.formItem_wrapper}>
                        <div className={style.formItem_bottomBlock}>
                            <FormItem name={'name'} label='ФИО'>
                                <Input<InheritancesListFormProps> control={control} name={'fio'}
                                                                  rules={{required: true}}/>
                            </FormItem>

                            {errors.fio && (<div className={style.error}>
                                <ErrorsMessage textMessage={'Это поле обязательное'}/>
                            </div>)}

                            <TakeOutButton color={'blue'}>
                                отправить
                            </TakeOutButton>
                        </div>
                    </div>
                </form>
                {error && (<ErrorsMessage textMessage={'Пользователь не найден, проверьте данные!'}/>)}

            </div>
            <div className={style.inheritance_itemBlock}>
                <h4>Наследники</h4>
                <div className={style.inheritance_item}>
                    {item ? (item.map((item) => (
                            <div key={item.id} className={style.inheritanceList}>
                                {/* <img src={face} alt="faceAvatar"/>*/}
                                <div className={style.contacts}>
                                    {/* <span className={style.name}>{name}</span>*/}
                                    <div className={style.contactsData}>
                                        {/*<span className={style.phone}>{phone}</span>*/}
                                        <span className={style.mail}>{item.email}</span>
                                    </div>
                                </div>
                                <DeleteContact onClick={() => deleteInheritance(item.id)}/>
                            </div>
                        ))) :
                        (<span className={style.description}>Список наследников пустой</span>)}
                </div>
            </div>
        </div>
    )
}


