import {createAsyncThunk} from '@reduxjs/toolkit';
import {baseRequestAxios} from 'api/baseRequest';

export const updateUserAvatarThunk = createAsyncThunk(
    'updateUserAvatar',
    async ({avatar}: {avatar: File | FormData}) => {
        const response = await baseRequestAxios.post<{success: boolean}>("/api/user/avatar", avatar, {
            headers: {
                'Content-Type': 'multipart/form-data',
            },
        })
        if (response.status === 200) {
            return response.data;
        }
    }
)
